@font-face {
  font-family: 'Sacramento';
  src: url('/fonts/Sacramento-Regular.ttf') format('truetype');
  font-style: cursive;
}
/* Border Radius */
html {
  position: relative;
  min-height: 100%;
}
body {
  background-color: #fff;
  text-rendering: optimizeLegibility;
  padding-top: 53px;
}
body #content a {
  font-weight: 300;
  color: #494949;
}
body a:hover {
  border-radius: 5px;
}
body#videoPage {
  color: white;
  margin-top: 53px;
}
.navbar .navbar-brand,
.videouri-title {
  font-family: 'Sacramento';
  font-weight: 300;
  font-size: 30px;
}
.navbar {
  border-radius: 0;
  border-bottom: 1px solid #e0e0e1;
  background-color: #fff;
}
.navbar .navbar-form {
  border: 0;
  padding: 0;
  margin: 0;
}
.navbar .navbar-form > .form-group {
  width: 100%;
}
.navbar .navbar-form > .form-group > .input-group {
  width: 100%;
  height: 53px;
}
.navbar .navbar-form > .form-group > .input-group input[type="text"].form-control,
.navbar .navbar-form > .form-group > .input-group input[type="text"].select2-search input[type="text"] {
  height: 53px;
}
.navbar .navbar-form > .form-group > .input-group .btn {
  border: none;
}
.navbar-right li a {
  font-weight: normal;
}
.navbar-right li a.join {
  background-color: #3C708D;
}
.navbar-right li a.join:hover {
  color: white;
}
/*
 * Sidebar
 */
#sidebar {
  /* Hide for mobile, show later */
  display: none;
  border-right: 1px solid #efefef;
  background: #f7f8fa;
}
#sidebar .nav-list li.nav-header {
  color: #16a085;
  margin-bottom: 10px;
}
#sidebar .nav-list li > a {
  font-size: 13px;
  font-weight: 300;
}
#sidebar .nav-list li > a i.fa {
  margin-right: 10px;
}
#sidebar .nav-list li.active > a,
#sidebar .nav-list li.active > a:hover,
#sidebar .nav-list li.active > a:focus {
  background-color: transparent;
  border-radius: 0;
  font-weight: 400;
}
@media (min-width: 768px) {
  #sidebar {
    position: fixed;
    top: 53px;
    bottom: -60px;
    left: 0;
    z-index: 10;
    display: block;
    padding: 20px;
    overflow-y: auto;
    overflow-x: hidden;
    border-right: 1px solid #eee;
  }
}
div#filter-options {
  margin-top: 10px;
  margin-bottom: 10px;
}
div#filter-options div.container {
  padding: 10px;
  background-color: #fff;
}
.video {
  margin-bottom: 20px;
  position: relative;
}
.video .image img {
  border-radius: 5px;
  margin: 0 auto;
  width: 100%;
}
.video .source {
  padding: 5px 10px;
  position: absolute;
  top: 5px;
  left: 5px;
  background: #000;
  color: #fff;
  font-weight: 700;
  border-radius: 3px;
  opacity: 0.3;
  transition: all .2s linear;
  -moz-transition: all .2s linear;
  -webkit-transition: all .2s linear;
  -o-transition: all .2s linear;
  -ms-transition: all .2s linear;
}
.video .source:hover {
  background-color: #D63E3D;
  opacity: 1.1;
  cursor: pointer;
}
.video .title {
  font-size: 20px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 10px 0;
}
/* Vide Page */
#video_data .info strong {
  font-weight: bold;
}
#video_data .info .tags {
  padding: 0;
  list-style: none;
  display: inline-block;
}
#video_data .info .tags li {
  float: left;
  display: inline;
}
#video_data .info .tags li a {
  color: #925C3E;
}
#video_data .info .tags li a:hover {
  text-decoration: underline;
  cursor: pointer;
}
#video_data .info .description {
  text-align: justify;
  padding: 0;
}
#video-info {
  padding: 15px 0;
  background: #222222;
}
#video-info .video-duration i.fa,
#video-info .video-views i.fa {
  vertical-align: middle;
}
#video-info .video-duration span,
#video-info .video-views span {
  font-size: 1.5em;
  vertical-align: middle;
  margin-left: 6px;
}
.footer {
  position: absolute;
  bottom: 0;
  z-index: 50;
  width: 100%;
  height: 60px;
  margin-top: 60px;
  background-color: #3c708d;
}
.footer a:hover {
  font-weight: bold;
  color: #bec6cd;
  text-decoration: none;
  text-shadow: 5px 1px 1px #296281;
}
.footer > .container {
  margin-top: 20px;
}
